@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;700&family=Smokum&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500;700&family=PT+Serif:ital@1&family=Smokum&display=swap');

$font-stack: 'Dancing Script', cursive;
$font-default: 'PT Serif', serif;
$body-color: #040405;
$color-default: #d8d8d8;
$gradient: #ff3a5e;

.nav {
    position: fixed;
    width: 100%;
    height: 70px;
    display: flex;
    background-color: $body-color;
    flex-direction: flex;
    left: 0px;
    border-bottom: 1px rgba(216,216,216, 0.5) solid; }


.nome {
    font-family: $font-stack;
    display: flex;
    flex-direction: flex;
    color: $gradient;
    font-size: 40px;
    align-items: center;
    margin-left: 2%; }

.links {
    font-family: $font-default;
    text-decoration: none;
    color: $color-default;
    font-size: 23px;
    margin-right: 60px; }

.links:hover {
      border-bottom: 2px #ff3a5e solid;
      box-shadow: 2px 2px #ff3a5e; }

.navLink {
    display: flex;
    flex-direction: flex;
    align-items: center;
    margin-left: 36%; }

@media (max-width: 1189px) {
    .nome {
        font-size: 30px; }
    .links {
        margin-right: 40px;
        font-size: 17px; }
    .nav {
        height: 60px; } }

@media screen and (max-width: 862px) {
    .nome {
        font-size: 25px; }
    .links {
        font-size: 15px; }
    .nav {
        height: 50px; } }



@media screen and (max-width: 770px) {
    .nome {
        font-size: 23px; }
    .links {
        margin-right: 25px; }
    .nav {
        height: 45px; } }
.btn-nav {
        display: none; }

@media screen and (max-width: 522px) {
    .links {
        display: none; }
    .btn-nav {
        display: block;
        font-size: 20px;
        background-color: $body-color;
        border: 0;
        color: $gradient; }
    .div-btn-nav {
        display: flex;
        flex-direction: column;
        margin-top: 3px; }

    .nome {
        font-size: 23px; }
    .nav {
        height: 30px;
        justify-content: space-between; }
    li {
         list-style: none; }
    .list-navL {
        margin-top: 1%;
        width: 113px;
        background-color: rgba(255,58,94, 0.5);
        text-align: center; }
    .links-nav:hover {
        color: #000000; }
    .links-nav {
        text-decoration: none;
        font-family: $font-default;
        color: $color-default; } }

@media screen and (max-width: 391px) {
    .nome {
        font-size: 18px; }
    .links-nav {
        font-size: 10px; }
    .list-navL {
        width: 90px; } }


